.about-us {
  width: 216px;
  height: 240px;
  margin: 0 auto;
  margin-bottom: 50px;

  @media screen and (min-width: 768px) {
    width: 688px;
    height: 60px;
  }

  @media screen and (min-width: 1680px) {
    width: 1344px;
    height: 102px;
    margin-bottom: 50px;
  }

  .list {
    @media screen and (min-width: 768px) {
      display: flex;
      justify-content: space-between;
    }
  }

  .item {
    display: flex;
    align-items: center;
    height: 60px;

    @media screen and (min-width: 320px) and (max-width: 767px) {
      &:not(:last-child) {
        margin-bottom: 30px;
      }
    }

    @media screen and (min-width: 1680px) {
      height: 102px;
    }

    .icon {
      width: 59px;
      height: 60px;

      @media screen and (min-width: 1680px) {
        width: 100px;
        height: 102px;
      }
    }

    .subtitle {
      margin-left: 15px;

      @media screen and (min-width: 1680px) {
        width: 300px;
        margin-left: -20px;
      }
    }
  }
}
