.programs {
  & .subtitle {
    font-size: 12px;
    line-height: 1.65;
    color: $accent-color;
  }

  & .subtitle-dark-theme {
    text-transform: uppercase;
    font-size: 14px;
    line-height: 2;
  }

 
  &-title {
    margin-bottom: 5px;
    vertical-align: top;
    transform: translateY(-10px);
  }

  &-subtitle {
    margin-bottom: 23px;
  }

  &-direction:not(:last-child) {
    margin-bottom: 8px;
  }

  &-subtitle::before {
    content: ' ';
    display: inline-block;
    width: 12px;
    height: 1px;
    background-color: $accent-color;
    margin-right: 5px;
    vertical-align: middle;
  }

  &-title::before {
    content: ' ';
    position: absolute;
    display: inline-block;
    width: 13px;
    height: 13px;
    border-radius: 50%;
    background-color: $accent-color;
    vertical-align: middle;

    transform: translate(-27px, 7px);
  }

  &-img {
    opacity: 0.5;
    position: absolute;
  }
}

@media screen and (max-width: 767px) {
  .programs {
    & .title-dark-theme {
      text-align: center;
      margin-bottom: 43px;
    }

    &-semester {
      padding-left: 20px;
    }

    &-semester:not(:last-child) {
      padding-bottom: 40px;
    }

    &-semester:last-child {
      padding-bottom: 50px;
    }

    &-list {
      margin-left: 74px;
  
    }

    &-img {
      transform: translate(180px, -90px);
    }
  }
}

@media screen and (min-width: 768px) {
  .programs {
    & .title-dark-theme {
      text-align: start;
      margin-bottom: 52px;
    }

    &-list {
      margin-left: 57px;
    }

    &-semester {
      padding-left: 50px;
    }

    &-semester:not(:last-child) {
      padding-bottom: 30px;
    }

    &-title::before {
      transform: translate(-57px, 7px);
    }
    &-section {
      display: flex;
    }

    &-direction:not(:last-child) {
      width: 176px;
      margin-right: 50px;
    }

    &-img {
      transform: translate(340px, -150px);
    }
  }
}

@media screen and (max-width: 1679px) {
  .programs {
    &-semester:not(:last-child) {
      border-left: 1px solid $secondary-background;
    }

    &-semester:last-child {
      border-left: 1px solid transparent;
    }
  }
}

@media screen and (min-width: 1680px) {
  .programs {
    & .title-dark-theme {
      margin-bottom: 160px;

      &::after {
        content: '';
        position: absolute;
        width: 100%;
        height: 1px;
        background-color: $secondary-background;
        top: 0;
        left: 0;
        transform: translate(0, 277px);
      }
    }

    &-title {
      margin-bottom: 25px;

      &::before {
      content: ' ';
      width: 17px;
      height: 17px;
      background-color: $accent-color;
      transform: translate(-6px, -60px);
    }
    }

    & .subtitle-dark-theme {
      text-transform: uppercase;
      font-size: 20px;
      line-height: 1.4;
    }

    & .subtitle {
      font-size: 20px;
    }

    &-subtitle {
      margin-bottom: 10px;

      &::before {
        content: ' ';
        width: 20px;
        height: 2px;
        margin-right: 10px;
      }
    }

    &-section {
      display: block;
    }

    &-list {
      display: flex;
      margin-left: 0;
    }

    &-semester {
      padding-left: 0;

      &:not(:last-child) {
      margin-right: 30px;  
      }

    }

    &-direction:not(:last-child) {
      width: 240px;
      margin-right: 0px;
    }


    &-title::before {
      content: ' ';
      width: 17px;
      height: 17px;
      background-color: $accent-color;
      transform: translate(-6px, -60px);
    }

    &-img {
      transform: translate(-183px, -198px);
    }
  }
}
