.page-heder .logo {
  position: relative;
  display: inline-block;
  width: 80px;
  height: 28.53px;
  background-image: url(.././images/svg/logo/logo-mobile-white.svg);

  @media screen and (min-width: 768px) {
    & {
      width: 128px;
      height: 45px;
      background-image: url(.././images/svg/logo/logo-desktop-white.svg);
    }
  }

  &.link {
    @media screen and (min-width: 1680px) {
      display: none;
    }
  }
}

.menu .logo {
  z-index: 100;
  width: 80px;
  height: 28.53px;
  background-image: url(.././images/svg/logo/logo-mobile-dark.svg);

  @media screen and (min-width: 768px) and (max-width: 1679px) {
    width: 128px;
    height: 45px;
    background-image: url(.././images/svg/logo/logo-tablet-dark.svg);
  }

  @media screen and (min-width: 1680px) {
    width: 128px;
    height: 45px;
    background-image: url(.././images/svg/logo/logo-desktop-white.svg);
  }
}
