.teacher {
  padding-top: 40px;
  padding-bottom: 50px;

  &-title {
    font-family: $main-font;
    font-weight: 800;
    font-size: 20px;
    line-height: 1.3;
    letter-spacing: 0.03em;
    color: $title-light-theme-color;
    margin-bottom: 20px;
  }

  &-img {
    margin-bottom: 20px;
  }

  &-name {
    font-family: $main-font;
    font-weight: 800;
    font-size: 20px;
    line-height: 1.2;
    letter-spacing: 0.08em;
    color: $title-light-theme-color;
    padding-bottom: 10px;
    border-bottom: 1px solid #b9b9b9;
  }

  &-advantages {
    padding-top: 20px;

    &-description {
      font-family: $main-font;
      font-weight: 600;
      font-size: 14px;
      line-height: 1.214;
      letter-spacing: 0.06em;
      color: $title-light-theme-color;
    }

    &-item {
      display: flex;

      &:not(:last-child) {
        margin-bottom: 27px;
      }
    }
  }

  &-group {
    /* flex-basis: 17px; height: 14px;*/
    max-width: 17px;
    max-height: 14px;
    margin-right: 9px;
  }

  &-tire {
    height: 38px;
  }

  &-tire-svg {
    width: 20px;
    height: 2px;
  }

  &-attainment {
    margin-bottom: 10px;

    &-description {
      font-family: $main-font;
      font-weight: 500;
      font-size: 12px;
      line-height: 1.429;
      color: $main-text-color;
    }

    &-item:not(:last-child) {
      margin-bottom: 25px;
    }
  }

  &-link {
    font-family: $main-font;
    font-weight: 500;
    font-size: 12px;
    line-height: 1.667;
    text-decoration-line: underline;
    color: $accent-color;
  }
}

@media screen and (max-width: 767px) {
  .teacher {
    &-advantages {
      padding-bottom: 5px;
    }
  }
}

@media screen and (min-width: 768px) {
  .teacher {
    padding-top: 50px;
    padding-bottom: 50px;

    &-img {
      margin-bottom: 0px;
    }

    &-title {
      font-size: 26px;
      line-height: 1.3;
      margin-bottom: 20px;
    }

    &-advantages-item:not(:last-child) {
      margin-bottom: 36px;
    }

    &-tire {
      height: 33px;
    }

    &-attainment {
      margin-top: 5px;

      &-description {
        font-size: 12px;
        line-height: 1.667;
      }
    }
  }

  .teacher .teacher-colum {
    display: flex;
  }
}

@media screen and (min-width: 768px) and (max-width: 1679px) {
  .teacher-right-side {
    width: 402px;
    margin-left: 40px;
  }
}

@media screen and (min-width: 1680px) {
  .teacher {
    padding-top: 130px;
    padding-bottom: 130px;

    &-title {
      font-size: 46px;
      line-height: 1.3;
    }
    &-right-side {
      width: 642px;
      margin-left: 30px;
    }

    &-name {
      font-size: 30px;
      line-height: 1.234;
      padding-bottom: 15px;
    }

    &-advantages {
      padding-top: 30px;
      padding-bottom: 15px;

      &-description {
        font-size: 16px;
        line-height: 1.25;
      }
    }

    &-group {
      max-width: 24px;
      max-height: 20px;
      margin-right: 35px;
    }

    &-attainment {
      padding-top: 15px;
      padding-bottom: 20px;

      &-description {
        font-size: 18px;
        line-height: 1.556;
      }

      &-item:not(:last-child) {
        margin-bottom: 27px;
      }
    }

    &-link {
      font-size: 18px;
      line-height: 1.556;
    }
  }
}
